.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
}

.App-ExchangeRate {
  display: flex;
  gap: 10px;
}

.App-ExchangeRate,
.App-Add小urrencyPair,
.App-CurrencyChains-UserInput {
  margin-bottom: 20px;
}

.App-CurrencyPairs {
  margin-top: 20px;
}

.App-CurrencyChains-UserInput,
.App-CurrencyChains-UserCurrency {
  display: flex;
  gap: 10px;
}

.App-CurrencyChains-Label {
  align-self: center;
  margin-right: 8px;
}

.App-Icon {
  display: inline;
  align-self: center;
}

@media (max-width: 600px) {
  .App {
    padding: 25px;
  }

  .App-ExchangeRate,
  .App-CurrencyChains-UserInput {
    flex-direction: column;
  }
}

@media (max-width: 400px) {
  .App {
    padding: 15px;
  }
}
